import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Session } from "./../../../services/services";
import { ClubProvider } from "./../../../providers/club-provider";
/**
 * Generated class for the MemberPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-member",
  templateUrl: "member.html"
})
export class MemberPage {
  currentUser: any = {
    pictureUrl: "assets/img/avatar-round.png"
  };
  isLogin: Boolean = false;
  club: any;

  //用于正式成员或普通成员分页
  isDataEmpty: boolean = false;
  isDataLoading: boolean = false;
  page: number = 1; // 页码
  pageCount: number = 10; // 每页条数
  hasMoreData: boolean = false; // 是否有更多数据
  clubVipMembers: any; //正式成员

  constructor(
    public navCtrl: NavController,
    public session: Session,
    public clubProvider: ClubProvider,
    public navParams: NavParams
  ) {
    if (this.session.user != null) {
      this.currentUser = this.session.user;
      this.isLogin = true;
    }
    this.club = this.navParams.data;
  }

  ionViewDidLoad() {
    this.getClubVipMembers();
  }
  openPersonMain(member) {
    this.navCtrl.push("PersonMainPage", member);
  }

  getClubVipMembers(_event?: any) {
    this.clubVipMembers = [];
    this.isDataLoading = true;

    // this.clubProvider.getEvents(this.pageCount).subscribe(res => {
    //   this.clubVipMembers = res;
    //   this.isDataLoading = false;
    //   this.isDataEmpty = !res || res.length == 0;
    //   this.hasMoreData = true;
    //   this.clubVipMembers.sort((a, b) => {
    //     return (
    //       new Date(b.eventTime).getTime() - new Date(a.eventTime).getTime()
    //     );
    //   });
    // });
  }

  doRefresh(_event) {
    _event.complete();
    this.getClubVipMembers();
  }

  doInfinite(_event) {
    // this.clubProvider.getEvents(this.pageCount).subscribe(res => {
    //   this.hasMoreData = Math.floor(Math.random() * 10) % 3 != 1;
    //   this.clubVipMembers.push.apply(this.clubVipMembers, res);
    //   _event.complete();
    // });
  }
}



